@charset "UTF-8";
* {
  font-family: "游ゴシック", "YuGothic", "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  scroll-behavior: smooth;
}

body {
  color: #333;
}
body.stop_scroll {
  overflow: hidden;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.6em;
}

a img {
  opacity: 1;
  transition: opacity 0.4s;
}
a:hover img {
  opacity: 0.8;
}
a.opacity {
  opacity: 1;
  transition: opacity 0.4s;
}
a.opacity:hover {
  opacity: 0.8;
}

main {
  overflow: hidden;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.center {
  text-align: center;
}

.flex {
  display: flex;
}

.f-wrap {
  flex-wrap: wrap;
}

.f-jc-sb {
  justify-content: space-between;
}

.f-jc-c {
  justify-content: center;
}

.f-jc-fe {
  justify-content: flex-end;
}

.f-ai-c {
  align-items: center;
}

.f-ai-fs {
  align-items: flex-start;
}

.f-ai-fe {
  align-items: flex-end;
}

.f-ac-c {
  align-content: center;
}

.base {
  width: 100%;
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

@media only screen and (min-width: 768px) {
  .sp {
    display: none !important;
  }
}
@media only screen and (max-width: 767px) {
  .pc {
    display: none !important;
  }
}
.flex {
  display: flex;
}

.hidden {
  display: none;
  visibility: hidden;
  opacity: 0;
}

header {
  font-family: "Cormorant Garamond", "Times New Roman", "YuMincho", "Hiragino Mincho ProN", "Yu Mincho", "MS PMincho", serif;
}
header .header__wrap {
  position: fixed;
  z-index: 99999;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header .header__logo {
  width: 100%;
  max-width: 191px;
  margin: 0 0 0 35px;
  fill: #fff;
  transition: fill 0.4s;
  padding: 19px 0 18px;
}
@media screen and (max-width: 767px) {
  header .header__logo {
    max-width: 103px;
    margin: 0 0 0 20px;
  }
}
header .header__logo.changeColor {
  fill: #231815;
}
header .header__contents {
  display: flex;
  width: 100%;
  max-width: 205px;
}
header .header__tel__btn {
  width: 100%;
  max-width: 140px;
  display: block;
  padding: 25px 0 24px;
  text-align: center;
  background: #000;
  color: #fff;
}
header .header__nav__btn {
  position: relative;
  width: 100%;
  max-width: 65px;
  background: #fff;
  cursor: pointer;
}
header .header__nav__btn.open span:first-child {
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
header .header__nav__btn.open span:not(:first-child, :last-child) {
  opacity: 0;
}
header .header__nav__btn.open span:last-child {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}
header .header__nav__btn span {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: 24px;
  height: 1px;
  background: #333;
  transform: translate(-50%, -50%);
  transition: all 0.4s ease-out;
}
header .header__nav__btn span:first-child {
  top: 35%;
  transform: translate(-50%, -50%);
}
header .header__nav__btn span:last-child {
  top: 65%;
  transform: translate(-50%, -50%);
}
header .nav {
  position: fixed;
  z-index: -999;
  width: 100vw;
  height: 100dvh;
  overflow-y: hidden;
  transition: all 0.4s ease-out;
  opacity: 0;
}
header .nav.active {
  z-index: 9999;
  opacity: 1;
}
header .nav__wrap {
  height: 100%;
}
@media screen and (max-width: 767px) {
  header .nav__wrap {
    flex-direction: column-reverse;
  }
}
header .nav__img__wrap {
  width: 100%;
  max-width: 62.7%;
  height: 50vh;
}
@media screen and (max-width: 767px) {
  header .nav__img__wrap {
    display: flex;
    height: 20%;
    max-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  header .nav__img__wrap {
    height: 80px;
  }
}
header .nav__img__wrap .img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
header .nav__img__wrap .img:nth-child(1) {
  background: url(../../img/common/nav01.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
}
header .nav__img__wrap .img:nth-child(2) {
  background: url(../../img/common/nav02.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}
header .nav__contents {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 37.3%;
  background: #fff;
}
@media screen and (max-width: 767px) {
  header .nav__contents {
    max-width: 100%;
    height: 100%;
    justify-content: center;
  }
}
header .nav__contents__wrap {
  padding: 0 0 0 108px;
}
@media screen and (max-width: 767px) {
  header .nav__contents__wrap {
    text-align: center;
    padding: 70px 0 0;
  }
}
header .nav__contents__wrap li {
  overflow: hidden;
  height: 1.6em;
}
header .nav__contents__wrap li:not(:last-child) {
  margin: 0 0 35px 0;
}
@media screen and (max-width: 767px) {
  header .nav__contents__wrap li:not(:last-child) {
    margin: 0 0 20px 0;
  }
}
header .nav__contents__wrap li a {
  display: block;
  font-size: 22px;
  font-weight: 500;
  letter-spacing: 0.03em;
  line-height: 1em;
  color: #000;
}
@media screen and (max-width: 767px) {
  header .nav__contents__wrap li a {
    font-size: 18px;
  }
}

footer {
  background: url(../../img/common/bg_footer01.jpg) no-repeat 50% 50%/cover;
  height: 100%;
}
footer .base {
  width: 100%;
  max-width: 353px;
  margin: 0 auto;
  padding: 52px 0;
  color: #fff;
}
footer .base p {
  font-family: "Noto Serif JP", "Times New Roman", "YuMincho", "Hiragino Mincho ProN", "Yu Mincho", "MS PMincho", serif;
  font-size: 14px;
  display: block;
  margin: 0 0 27px 0;
}
@media screen and (max-width: 767px) {
  footer .base p {
    font-size: 12px;
  }
}
footer .base .name__wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin: 0 0 45px 0;
}
footer .base .name__wrap .footer__logo {
  width: 100%;
  max-width: 190px;
}
@media screen and (max-width: 767px) {
  footer .base .name__wrap .footer__logo {
    max-width: 155px;
  }
}
footer .base .name__wrap h2 {
  font-family: "Noto Sans JP", "游ゴシック", "YuGothic", "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  font-size: 14px;
  letter-spacing: 0.08em;
  line-height: 1em;
}
@media screen and (max-width: 767px) {
  footer .base .name__wrap h2 {
    font-size: 12px;
  }
}
footer .base small {
  font-family: "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  display: block;
  font-size: 10px;
  font-weight: 400;
  line-height: 1em;
  letter-spacing: 0.08em;
}
@media screen and (max-width: 767px) {
  footer .base small {
    font-size: 9px;
  }
}